/* src/styles/PixelOdyssey.css */
body {
  margin: 0;
  font-family: 'Press Start 2P', cursive;
  background: #000;
  color: #fff;
  overflow-x: hidden;
}

.pixel-odyssey {
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  position: relative;
}

/* CRT Scanline Overlay */
.pixel-odyssey::before {
  content: '';
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: repeating-linear-gradient(
    transparent 0%,
    transparent 2px,
    rgba(0, 0, 0, 0.1) 2px,
    rgba(0, 0, 0, 0.1) 4px
  );
  pointer-events: none;
  z-index: 1000;
}

/* Header */
.hero {
  background: #000;
  padding: 60px 20px;
  text-align: center;
  position: relative;
}

.title {
  font-size: 2em;
  color: #00FFFF;
  text-shadow: 0 0 10px #00FFFF, 0 0 20px #FF00FF;
  animation: glow 2s infinite;
}

.tagline {
  font-size: 1em;
  color: #FF00FF;
  margin: 20px 0;
}

.start-button {
  background: #00FFFF;
  color: #000;
  border: 4px solid #FF00FF;
  padding: 10px 20px;
  font-family: 'Press Start 2P', cursive;
  cursor: pointer;
  animation: pulse 1s infinite;
}

@keyframes glow {
  0%, 100% { text-shadow: 0 0 10px #00FFFF, 0 0 20px #FF00FF; }
  50% { text-shadow: 0 0 20px #00FFFF, 0 0 30px #FF00FF; }
}

@keyframes pulse {
  0%, 100% { transform: scale(1); }
  50% { transform: scale(1.1); }
}

.avatar {
  width: 50px;
  height: 50px;
  background: #00FFFF;
  position: absolute;
  bottom: 20px;
  left: 50%;
  transform: translateX(-50%);
  animation: walk 1s infinite steps(4);
}

@keyframes walk {
  0% { background-position: 0 0; }
  100% { background-position: -200px 0; }
}

/* Mini-Map (Navigation) */
.mini-map {
  position: sticky;
  top: 0;
  background: #000;
  padding: 10px 0;
  text-align: center;
  z-index: 999;
}

.mini-map a {
  color: #00FFFF;
  margin: 0 15px;
  text-decoration: none;
  font-size: 0.8em;
}

.mini-map a:hover {
  color: #FF00FF;
}

/* Sections */
section {
  padding: 40px 20px;
  max-width: 1000px;
  margin: 0 auto;
  background: #111;
  border: 4px solid #00FFFF;
  margin-bottom: 20px;
}

/* About */
.about-signpost {
  background: #222;
  padding: 20px;
  border: 4px solid #FF00FF;
  animation: slideIn 1s ease-out;
}

@keyframes slideIn {
  from { transform: translateX(-100%); opacity: 0; }
  to { transform: translateX(0); opacity: 1; }
}

/* Skills */
.skill-powerups {
  display: flex;
  flex-wrap: wrap;
  gap: 20px;
  justify-content: center;
}

.powerup {
  background: #00FFFF;
  color: #000;
  padding: 10px 20px;
  border: 4px solid #FF00FF;
  cursor: pointer;
  transition: transform 0.3s;
}

.powerup:hover {
  transform: translateY(-10px);
}

/* Projects */
.project-chests {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(250px, 1fr));
  gap: 20px;
}

.chest {
  background: #222;
  padding: 20px;
  border: 4px solid #00FFFF;
  transition: transform 0.3s;
}

.chest:hover {
  transform: scale(1.05);
}

.chest a {
  color: #FF00FF;
}

/* Contact */
.contact-terminal {
  background: #222;
  padding: 20px;
  border: 4px solid #FF00FF;
}

.contact-terminal input,
.contact-terminal textarea {
  background: #000;
  color: #00FFFF;
  border: 2px solid #00FFFF;
  padding: 10px;
  margin: 10px 0;
  width: 100%;
  font-family: 'Press Start 2P', cursive;
}

.contact-terminal button {
  background: #FF00FF;
  color: #000;
  border: 4px solid #00FFFF;
  padding: 10px 20px;
  font-family: 'Press Start 2P', cursive;
  cursor: pointer;
}

.contact-terminal button:hover {
  background: #00FFFF;
}

/* Footer */
.footer {
  background: #000;
  padding: 20px;
  text-align: center;
}

.footer button {
  background: #00FFFF;
  color: #000;
  border: 4px solid #FF00FF;
  padding: 10px 20px;
  font-family: 'Press Start 2P', cursive;
  cursor: pointer;
}